.layout-container {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    margin-left: 2em;
    margin-right: 0.5em;
    max-width: 100%;
}

.image-container {

    height: 80%;
    width: 100%;
    max-height: 30em;
    max-width: 100%;
    /* max-width: 60em;
    max-height: 30em; */


}



.layout-options-container {
    display: flex;

}

svg {
    grid-column: 1 / 14;
    /* width:100%;
    height: 100%; */
    border:1px solid black;
    overflow-y: scroll;
    
    
}
svg *{
    fill:rgba(255, 145, 0, 0.534);
    
    
}