/* CSS Variables */
:root {
  --primary: rgb(208, 219, 219);
  --dark: #333;
  --light: #fff;
  --shadow: 0 1px 5px rgba(104, 104, 104, 0.8);
  --dark1: #5d5c61;
  --grey1: #68b3b6a8;
  --lighterblue: #95adbe;
  --darkerblue: #557a95;
  --secondary: #b1a296;
}



.App {
  text-align: center;
  box-sizing: border-box;
  font-family: Helvetica, sans-serif;
  color: black;
  margin: 30px;
}



body {
  background: var(--grey1);
  margin: 30px 50px;
  line-height: 1.4;
}


/* ##################  Header ####################### */ 
/* ################################################## */
/* ######### HEADER ################# HEADER ######## */

.site-header {
  /* background-color: var(--dark); */
  background-color: var(--dark);
  display: grid;
  grid-template-columns: 0.7fr 0.7fr 1fr 1fr 1fr;
  
}


.site-header h1 {
  font-size: 3em;
  color: lightgray;
  grid-column-start: 1;
  grid-column-end: 3;
  margin: 0px;
}

.site-header .vertical-divider {
  width: 3px;
  height: 70%;
  align-self: center;
  background-color: black;
}


.site-header .header-strip {
  grid-row-start: 2;
  grid-column-start: 1;
  grid-column-end: 6;
  background-color: var(--primary);
}

.site-header .sub1, .sub2 {
  color: black;
  grid-row-start: 2;
  grid-row-end: 2;
  margin: 0px;
  padding: .6em;
}

.site-header .sub1 {
  grid-column-start: 1;
  grid-column-end: 1;
}
.site-header .sub2 {
  grid-column-start: 2;
  grid-column-end: 2;
}


/* ##################  Navigation ########################### */ 
/* ########################################################## */
/* ######### Navigation ################# Navigation ######## */


.main-nav ul {
  display: grid;
  grid-gap: 20px;
  padding: 0;
  list-style: none;
  grid-template-columns: repeat(5, 1fr);
}

.main-nav li {
  display: flex;
  justify-content: center;

  background-color: var(--primary);
  text-align: center;
}

.main-nav span {

  display: flex;
  align-items: center;

}

.main-nav a {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  margin: auto;
  padding: 0.1em;
  text-decoration: none;
  color: var(--dark);
  text-transform: uppercase;
  font-size: 1.2rem;
  text-align: center;

}


.main-nav a:hover {
  background: var(--dark);
  color: var(--light);
}




/* ################## Main Content ############################## */ 
/* ############################################################## */
/* ######### Main Content ################# Main Content ######## */




.main-content {
  display: grid;
  background-color:var(--primary);
  grid-template-columns: 1fr 12.5fr 2.5fr;
  grid-template-rows: 1fr 8fr 2fr;
  height: 40em;
  border: 1px solid black;
}

.section-header {

  background-color: var(--dark);
  color: var(--primary);
  padding: 0.3em;
  margin: 0;
  grid-column: 1 / 4;
  grid-row: 1;
}

.section-header h1 {
  grid-column: 2 / 4;
  grid-row: 1;
  padding: 0.1;
  margin: 0;
}

.info-panel-container {
  grid-row: 2 / 3;
  grid-column: 3 / 4;
  background-color: var(--dark);
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  text-align: center;
}

.info-panel-container button {
  height: min-content;
  width: auto;
  padding: 1em;
  margin: 0.2em 0;
}


.info-panel-container .link{
  width: auto;
  height: min-content;
  margin: 0.2em 0;
  padding: 1em;
  text-align: center;
  border: 1px solid black;
  text-decoration: none;
  background-color: var(--primary);
  color:black;
}


.info-panel-container p{

  width: auto;
  margin: auto;
  height: 100%;
  padding: 7em 1em;
  border: 1px solid black;
  text-decoration: none;
  background-color: var(--primary);
  color:black;
}

.notes-container {
  grid-row: 3 / 4;
  grid-column: 1 / 4;
  color: var(--primary);
  background-color: var(--dark);
  border: 1px solid black;
  overflow-y: scroll;
  white-space: pre-line;
  min-height: 10em;
  background-color: var(--dark);
  
  
}

.note {
  margin: auto;
  margin-top: 1em;
  padding: 1em;
  border: 1px solid black;
  width: min-content;
  background-color: white;
}

.notes-container p {
  text-align: left;
  margin: auto;
  margin-top: 0.5em;
  width: 25em;
  height: stretch;
  color: black;
  /* background-color: whitesmoke; */
}


.main-content-nav {
  grid-column: 1;
  grid-row: 2;
  display: grid;
  align-items: center;
}

.main-content-nav ul {
  display: grid;
  gap: 20px;
  padding: 0;
  list-style: none;
  grid-template-rows: repeat(auto, 1fr);
}


.main-content-nav a {
  background: var(--lighterblue);
  display: block;
  text-decoration: none;
  padding: 0.8rem;
  text-align: center;
  color: var(--dark);
  text-transform: uppercase;
  font-size: 1.1rem;
  box-shadow: var(--shadow);
}

.main-content-nav a:hover {
  background: var(--dark);
  color: var(--light);
}

.active {
  background-color: indianred;
  color: white;
}

/* FOR APPLICATION OVERVIEW AND PROCESS OVERVIEW */

.main-overview {
  text-align: left;
  margin-right: 0em;
  width: auto;
  max-height: 80%;
  grid-column: 2/3;
  grid-row: 2 / 3;
  overflow-y: scroll;
  padding: 2em;
}



.main-overview p + p {
  margin-top: 2em;
}

.main-overview h3 + p {
  margin-top: 3em;
}

.main-overview p + h3 {
  margin-top: 3em;
}

/* FOR THE DETAILED OVERVIEWS  */


.overview-container {
  grid-column: 2;
  grid-row: 2 / 3;
  overflow-y: scroll;
  padding: 0em 3em;
}

.overview-container .image:hover {
  height: 200%;
}



.overview-container ul p{
  padding-left: 0;

}

.overview-container h1 {
  padding: 2em 1em;
}

.overview-container .last {
  padding-bottom: 10em;
}

.overview-container p {
  padding: 1em 4em;
  text-align: left;
}

.overview-container table {
  margin: auto;
  
}


.overview-container table, th, td {
  border: 1px solid black;
  border-collapse: collapse;
  margin-bottom: 1em;

}

.process-container .image:hover {
  height: 200%;
  width: 200%;

}


.process-container {
  grid-column: 2 / 3;
  grid-row: 2 / 3;
  overflow-y: scroll;
  padding: 2em;
  
}

.process-container p {
  padding: 1em 4em;
  text-align: left;
}

.process-container h2, h3 {
  text-align: center;
}

.process-container ol {
  margin: auto;
  text-align: left;
  
}

.process-container li {
  padding-bottom: 1em;
}

